@import url('https://fonts.googleapis.com/css?family=Montserrat:300,300i,400,500');
/* @import url('https://fonts.googleapis.com/css?family=Nunito+Sans:200,200i,300,400,700'); */
@import url('./animations.css');

::-moz-selection {
  color:white;
  background: #42A5F5;
}

::selection {
  color:white;
  background: #42A5F5;
}

html {
  font-size: 20px;
}

body {
  font-family: 'Montserrat', sans-serif;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0
}

a {
  color: inherit;
  text-decoration: none;
}

input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 30px white inset;
}

p {
  margin-top: 0
}



